body {
	font-family:
		Noto Sans TC,
		微軟正黑體;
	overflow: auto !important;
	padding-right: 0 !important;
}
.container {
	max-width: 1045px;
}
header {
	height: 81px;
	@include PC-sm {
		height: 70px;
	}
	@include Pad {
		height: 62px;
	}
}
/*--------------------主選單開始------------------*/
.navbar {
	background: $white;
	border-bottom: 3px solid $primary-color;
	align-items: center;
	padding-right: 0 !important;
	z-index: 1080;
	&-brand {
		@include PC-sm {
			width: 120px;
		}
		@include Pad {
			width: 100px;
		}
		@include mobile-md {
			width: 80px;
			margin-right: $w-xs;
		}
	}
	&-title {
		font-size: $font-lg;
		font-weight: $font-light;
		color: $gray900;
		@include Pad {
			font-size: $font-sm !important;
		}
		@include mobile-md {
			font-size: $font-xs !important;
		}
	}
	&-collapse {
		position: absolute;
		width: 100%;
		left: 0;
		top: calc(100% + 8px);
		z-index: 1080;
		@include PC-sm {
			flex-shrink: 1;
		}
		@include Pad {
			background: $primary-color;
			position: absolute;
			left: 0;
			width: 100%;
		}
	}
	&-collapse[data-con="gis"] {
		top: 79px;
		left: 50%;
		max-width: 1069px;
		margin-left: -534px;
		z-index: 1080;
		@media (max-width: 1069px) {
			width: calc(100% - 16px);
			margin-left: 0;
			left: 8px;
		}
		@include PC-sm {
			top: 68px;
		}
		@include Pad {
			top: 60px;
		}
	}
	&-function {
		display: flex;
		padding: 0 $w-xs;
		flex-direction: column;
		border-right: 1px solid $gray400;
		flex-shrink: 0;
		@include PC {
			display: none;
		}
		&:first-child {
			margin-left: auto;
		}
		&-title {
			display: flex;
			justify-content: center;
			font-size: $font-xsm;
			font-weight: $font-light;
			img {
				margin-right: $w-xxxs;
			}
		}
		&-list {
			display: flex;
		}
		&-item {
			&:last-child {
				.navbar-function-link {
					padding-right: 0;
				}
			}
		}
		&-link {
			position: relative;
			color: $gray900;
			padding: 0 $w-xs 0 $w-xs;
			&:hover {
				color: $primary-color;
			}
			&::before {
				content: "";
				display: block;
				position: absolute;
				width: 4px;
				height: 13px;
				left: 4px;
				top: 6px;
				background: $primary-color;
			}
		}
	}
	&-nav {
		display: flex;
		flex-direction: row;
		flex-shrink: 0;
		padding: 0 0 0 $w-xxs;
		margin-left: auto;
	}
	&-mobile {
		/*往上捲動選單*/
		display: none;
		margin-left: auto;
		&-item {
			&:last-child {
				.navbar-mobile-link {
					margin-right: 0;
				}
			}
		}
		&-link {
			color: $primary-color;
			padding: 0 $w-xxs;
			&:hover {
				color: $gray700;
			}
		}
	}
	&-dropdown {
		/*下拉選單*/
		display: flex;
		justify-content: space-between;
		text-align: center;
		background: $white;
		border: 1px solid $gray500;
		@include Pad {
			overflow: auto;
		}
		&-item {
			@include Pad {
				position: relative;
				margin-bottom: $w-sm;
				padding-bottom: $w-xxxs;
				&::after {
					content: "";
					position: absolute;
					left: 6px;
					bottom: 0;
					display: block;
					width: calc(100% - 12px);
					height: 1px;
					background: $primary-color;
				}
				&:nth-last-child(1),
				&:nth-last-child(2) {
					&::after {
						display: none;
					}
				}
			}
			&[data-con="locate"] {
				display: flex;
				align-items: center;
				margin-bottom: $w-lg;
				position: relative;
				@include mobile-md {
					flex-direction: column;
					align-items: start;
					margin-bottom: 0;
				}
				&:after {
					display: none;
				}
				.nav-link {
					@include mobile-md {
						margin: $w-sm auto;
					}
					img {
						width: $w-md;
					}
				}
				.form-dropdown-content {
					min-width: auto;
				}
			}
		}
		&-title {
			font-size: $font-smd;
			color: $gray900;
			border-bottom: 1px solid $primary-color;
			padding-bottom: $w-xxs;
			@include PC-sm {
				font-size: $font-sm;
			}
			@include Pad {
				font-size: $font-xsm;
				border-bottom: 0;
				padding-bottom: 0;
			}
		}
		&-link {
			display: block;
			color: $gray700;
			margin-bottom: $w-xxs;
			&:hover {
				color: $gray700;
				opacity: 0.8;
			}
			@include PC-sm {
				font-size: $font-xsm;
			}
			@include Pad {
				font-size: $font-xs;
			}
		}
	}
}
.input-group-dropdown {
	font-size: $font-xs;
	li {
		padding: $w-xxxs $w-sm;
		cursor: pointer;
		&:hover {
			background: $primary-light-color;
		}
	}
}
/*-----------------麵包屑開始------------------*/
.breadcrumb {
	.form-select {
		display: inline-block;
		width: 75px;
		background: url("../images/ic-dropdown-width.svg") $white no-repeat right center;
		border-radius: $w-xxxs;
		padding: 0 $w-md;
		border: 0;
		color: $primary-color;
		font-size: $font-xs;
		text-align: center;
	}
}
/*-----------------右側選單開始------------------*/
.nav {
	&-item {
		flex-shrink: 0;
	}
	&-link {
		display: flex;
		flex-direction: column;
		align-items: center;
		color: $primary-color !important;
		padding: 0 $w-xxs !important;
		font-size: $font-xsm;
		@include PC-sm {
			color: $white;
			text-align: center;
			font-weight: $font-light;
			img {
				width: 30px;
			}
			p {
				font-size: $font-xs;
			}
		}
		@include Pad {
			img {
				width: 25px;
			}
			p {
				font-size: $font-xxs;
			}
		}
		&:hover {
			opacity: 0.8;
			@include Pad {
				color: $white;
			}
		}
	}
	&-toggler {
		position: relative;
		@include mobile-lg {
			padding: 0 !important;
		}
		&:after {
			content: "";
			position: absolute;
			width: calc(100% + 8px);
			height: 40px;
			left: -4px;
			bottom: -16px;
			background: $primary-color;
			border-radius: $w-xxxs $w-xxxs 0 0;
		}
		&.collapsed {
			&:after {
				display: none;
			}
			p {
				color: $primary-color;
			}
			.nav-toggler-bar {
				transform: rotate(0deg);
				width: 22px;
				@include Pad {
					width: 20px;
				}
			}
			.nav-toggler-bar3 {
				top: 8px;
				@include PC-sm {
					top: 4px;
				}
				@include Pad {
					top: 2px;
				}
			}
			.nav-toggler-bar2 {
				top: 15px;
				opacity: 1;
				@include PC-sm {
					top: 11px;
				}
				@include Pad {
					top: 9px;
				}
			}
			.nav-toggler-bar1 {
				top: 23px;
				@include PC-sm {
					top: 19px;
				}
				@include Pad {
					top: 15px;
				}
			}
		}
		&-img {
			width: 34px;
			height: 34px;
			position: relative;
			@include PC-sm {
				width: 30px;
				height: 30px;
			}
			@include Pad {
				width: 25px;
				height: 25px;
			}
		}
		p {
			color: $white;
			position: relative;
			z-index: 3;
		}
		&-bar {
			top: 13px;
			left: 4px;
			width: 24px;
			height: 2.5px;
			position: absolute;
			background: $primary-color;
			@include PC-sm {
				top: 8px;
				width: 20px;
			}
			@include mobile-md {
				top: 8px;
				width: 18px;
				height: 2px;
			}
		}
		&-bar1 {
			transform: rotate(-45deg);
		}
		&-bar2 {
			opacity: 0;
		}
		&-bar3 {
			transform: rotate(45deg);
		}
	}
}
/*-----------------主選單收合開始------------------*/
.navbar.up {
	padding-top: 0;
	padding-bottom: 0;
	@include Pad {
		padding-top: $w-xxxs;
		padding-bottom: $w-xxxs;
	}
	.navbar-brand {
		width: 90px;
		@include Pad {
			width: 80px;
		}
		@include mobile-md {
			width: 70px;
			margin-right: $w-xs;
		}
	}
	.navbar-title {
		font-size: $font-md;
		display: block !important;
	}
	.navbar-mobile {
		display: flex;
		@include PC-sm {
			display: none;
		}
	}
	.navbar-collapse {
		top: calc(100% + 2px);
		@include Pad {
			top: calc(100% + 7px);
		}
		@include mobile-xmd {
			top: 100%;
		}
	}
	.navbar-function {
		display: none;
	}
	.navbar-nav {
		img {
			display: none;
		}
	}
	.navbar-nav[data-con="mis"] {
		display: none;
		@include PC-sm {
			display: flex;
			img {
				display: block;
			}
			p {
				display: none;
			}
		}
	}
	.nav-toggler {
		&:after {
			display: none;
		}
		@include mobile-md {
			margin-top: 2px;
		}
	}
	.nav-toggler.collapsed {
		.nav-toggler-bar3 {
			@include PC-sm {
				top: 4px;
			}
		}
		.nav-toggler-bar2 {
			@include PC-sm {
				top: 11px;
			}
		}
		.nav-toggler-bar1 {
			@include PC-sm {
				top: 18px;
			}
		}
	}
	.nav-toggler-bar {
		@include PC-sm {
			top: 12px;
			width: 22px;
		}
		@include mobile-md {
			top: 8px;
			width: 18px;
			height: 2px;
		}
	}

	.nav-link {
		font-weight: $font-light;
		img {
			display: none;
		}
		&:hover {
			color: $secondary-light-color;
		}
	}
	.btn {
		background: $white !important;
		color: $primary-color !important;
		border: 1px solid $white;
		&:hover {
			background: $alert-color !important;
			color: $white !important;
		}
	}
}

/*--------------------footer開始------------------*/
footer {
	margin-top: 72px;
	position: relative;
}
.footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	color: $white;
	font-size: $font-xsm;
	font-weight: $font-light;
	@include PC {
		flex-direction: column;
	}
	@include PC-md {
		font-size: $font-xs;
	}
	@include Pad {
		font-size: $font-xxs;
		flex-direction: column;
	}
	&-logo {
		width: 210px;
		@include PC {
			width: 180px;
		}
		@include Pad {
			width: 150px;
		}
	}
	&-nav {
		display: flex;
	}
	&-item {
		&:last-child {
			.footer-link {
				border-right: 0;
			}
		}
	}
	&-link {
		color: $white !important;
		padding: 0 $w-sm;
		border-right: 1px solid $white;
		&:hover {
			opacity: 0.8;
		}
	}
	&-bottom {
		width: 100%;
		flex-shrink: 0;
		display: flex;
		justify-content: space-between;
		text-align: center;
		font-size: $font-xs;
		@include Pad {
			font-size: $font-xxs;
		}
		@include PC-sm {
			align-items: center;
			flex-direction: column;
		}
		p:last-child {
			font-size: $font-xxxs;
		}
	}
}
.gotop {
	position: fixed;
	right: calc(50% - 512px);
	bottom: 170px;
	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: $white;
	border-radius: 10px;
	opacity: 0;
	border: 1px solid $primary-color;
	pointer-events: none;
	@include PC-sm {
		width: 44px;
		height: 44px;
		line-height: 44px;
		font-size: $font-xxs;
		right: 12px;
		bottom: 68px;
	}
	@include Pad {
		width: 36px;
		height: 36px;
		line-height: 36px;
		position: absolute;
		bottom: auto;
		top: -50px;
	}

	&:hover {
		color: $white;
		background: $alert-light-color;
		img {
			filter: invert(100%) sepia(0%) saturate(7469%) hue-rotate(68deg) brightness(500%) contrast(97%);
		}
	}

	&.show {
		opacity: 1;
		pointer-events: initial;
	}
}
/*--------------------視窗開始------------------*/
.modal {
	padding-top: 100px;
	&-content {
		overflow: hidden;
	}
	&-header {
		border: 0;
		display: flex;
		justify-content: center;
		position: relative;
		border-bottom: 1px solid $gray400;
		background: $white;
		button {
			position: absolute;
			right: 0;
			top: 12px;
			background-size: 12px;
			&:focus,
			&:active {
				border: none !important;
			}
		}
	}
	&-title {
		display: flex;
		justify-content: center;
		color: $primary-color;
	}
	&-subtitle {
		display: flex;
		flex-wrap: wrap;
		font-size: $font-smd;
		font-weight: $font-heavy;
		@include Pad {
			flex-direction: column;
			font-size: $font-sm;
		}
		p > span {
			margin-left: $w-xs;
			padding-left: $w-xs;
			border-left: 1px solid $gray300;
			@include Pad {
				width: 100%;
				display: block;
				padding-left: 0;
				margin-left: 0;
				border-left: 0;
			}
		}
		p ~ span {
			display: block;
			font-size: $font-sm;
			font-weight: $font-light;
			@include Pad {
				font-size: $font-xs;
			}
		}
		&[data-con="less"] {
			@include Pad {
				flex-direction: row;
				align-items: center;
			}
			& ~ p {
				font-size: $font-xsm;
				@include Pad {
					font-size: $font-xs;
				}
			}
			p > span {
				@include Pad {
					width: auto;
					display: inline-block;
					margin-left: $w-xs;
					padding-left: $w-xs;
					border-left: 1px solid $gray300;
				}
			}
		}
	}
	&-body {
		background: $white;
		font-size: $font-sm;
		min-height: 100px;
	}
	&-footer {
		background: $white;
		border: 0;
		display: flex;
		justify-content: center;
	}
	&-action {
		border-radius: $w-xxxs;
		letter-spacing: 2px;
		max-width: 200px;
	}
	.form-control {
		border: 1px solid $primary-color;
		border-radius: $w-xxxs;
	}
	.input-group {
		button {
			border: 1px solid $primary-color;
			border-radius: $w-xxs;
			display: flex;
			align-items: center;
			&:focus {
				border: 1px solid $primary-color;
			}
		}
	}
}
.modal-body {
	&-title {
		font-size: $font-md;
		@include PC {
			font-size: $font-smd;
		}
		@include Pad {
			span {
				width: 100% !important;
				padding-left: 0 !important;
				margin-left: 0 !important;
				border-left: 0 !important;
				margin-top: 4px;
			}
		}
	}
}
.modal-list {
	display: flex;
	border: 1px solid $gray300;
	@include Pad {
		flex-direction: column;
	}
	&-title {
		flex-shrink: 0;
		border-right: 1px solid $primary-color;
		padding: 0 $w-xs;
		margin: $w-xs 0;
		width: 45px;
		text-align: center;
		font-size: $font-smd;
		@include Pad {
			width: 100%;
			position: sticky;
			top: 0;
			border-right: 0;
			border-bottom: 1px solid $primary-color;
			margin: 0;
			padding: $w-xxs 0;
			font-size: $font-sm;
			& ~ ul {
				height: 135px !important;
			}
		}
	}
	&-link {
		display: flex;
		justify-content: space-between;
		color: $gray800;
		border-bottom: 1px solid $white;
		padding: $w-xxxs $w-xs $w-xxxs $w-xs;
		@include Pad {
			font-size: $font-xsm;
			padding: 6px $w-xs;
			flex-wrap: wrap;
			span {
				width: 100%;
			}
		}
		&:hover {
			background: $white;
			color: $primary-color;
			span {
				color: $primary-dark-color !important;
			}
		}
	}
}
.modal-show-box {
	border: 1px solid $gray300;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	&.none {
		background: $white;
		&:after {
			content: "點選照片清單進行預覽";
			display: flex;
			align-items: flex-end;
			height: 110px;
			justify-content: center;
			color: $gray400;
			background: url("../images/sign-photo.svg") no-repeat top center;
			background-size: 75px;
			@include PC-sm{
				background: url("../images/sign-photo.svg") no-repeat center 20px;
				background-size: 50px;
				height: 100px;
				margin-bottom: 20px;
			}
		}
	}
}
.modal-combo-wrapper {
	display: flex;
	justify-content: space-between;
	@include PC {
		flex-wrap: wrap;
	}
}
.modal-combo-box {
	font-size: $font-xsm;
	display: flex;
	flex-direction: column;
	margin-right: $w-sm;
	@include PC {
		margin-bottom: $w-sm;
	}
	@include PC-sm {
		width: calc(50% - 10px) !important;
	}
	@include Pad {
		width: 100% !important;
	}
	&:nth-child(1) {
		width: 26%;
		@include PC {
			width: calc(33.33% - 10px);
			margin-right: 0;
		}
	}
	&:nth-child(2) {
		width: 27%;
		@include PC {
			width: calc(33.33% - 10px);
			margin-right: 0;
		}
	}
	&:nth-child(3) {
		width: 21%;
		@include PC {
			width: calc(33.33% - 10px);
			margin-right: 0;
		}
	}
	&:nth-child(4) {
		width: 26%;
		margin-right: 0;
		@include PC {
			width: 100%;
		}
	}
	&-header {
		display: flex;
		align-items: center;
		border-bottom: 3px solid $primary-color;
		padding-bottom: $w-xs;
		font-size: $font-smd;
		margin-bottom: 0;
		flex-shrink: 0;
	}
	&-body {
		background: $primary-xlight-color;
		border: 1px solid $gray300;
		padding: $w-sm;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	&-list {
		&-link {
			display: flex;
			flex-direction: column;
			color: $gray800;
			position: relative;
			border-bottom: 1px solid $gray300;
			padding: $w-xs 0 $w-xs $w-sm;
			&:hover {
				color: $primary-color;
			}
			&:after {
				content: "";
				display: block;
				position: absolute;
				width: 3px;
				height: $w-sm;
				background: $primary-color;
				left: 0;
				top: $w-sm;
			}
			span {
				padding-left: $w-xxs;
				margin-left: $w-xxs;
				border-left: 1px solid $gray300;
				line-height: 1;
				display: inline-block;
			}
		}
	}
	.modal-combo-box-info-list {
		&-item {
			display: flex;
			flex-direction: column;
			align-items: center;
			font-size: $font-xxs;
			img {
				margin-bottom: $w-xxxs;
			}
			p {
				font-size: $font-xs;
				font-weight: $font-heavy;
			}
		}
	}
}
.modal-combo-wrapper.none {
	.modal-combo-box:first-child {
		.modal-combo-box-header {
			height: 40px;
		}
	}
	.modal-combo-box-icon {
		-webkit-filter: grayscale(0);
		opacity: 1;
		width: 20px;
	}
	.modal-combo-box-header {
		padding-bottom: $w-xxxs;
		img {
			-webkit-filter: grayscale(0);
			opacity: 1;
		}
	}
	.modal-combo-box-body {
		a,
		button {
			pointer-events: none;
		}
		button {
			color: $gray600 !important;
		}
		img {
			-webkit-filter: grayscale(1);
			opacity: 0.5;
		}
	}
	.info-list-item {
		color: $gray600;
		span {
			color: $gray600 !important;
		}
		div {
			font-weight: $font-light;
		}
	}
	.modal-combo-box-info-list {
		color: $gray600;
	}
}

.modal-btn-list {
	border: 1px solid $primary-color;
	&-item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: $w-xxs $w-xs;
		background: $white;
		border-bottom: 1px solid $gray200;
		@include Pad {
			font-size: $font-xs;
		}
		&:hover {
			color: $primary-color;
		}
		&.active {
			background: $primary-light-color;
		}
	}
	&.none {
		display: flex;
		justify-content: center;
		align-items: center;
		border: 1px solid $gray300 !important;
		background: $white;
		&:after {
			content: "請新增照片";
			color: $gray400;
		}
	}
}
.modal-btn-list[data-con="flat"] {
	&.none {
		&:after {
			content: "請新增平面圖";
		}
	}
}

.modal.fade.show {
	display: block;
	opacity: 1;
}
.modal-backdrop {
	background-color: rgba(0, 0, 0, 0.3); /* 設定背景顏色及透明度 */
}
.border-fix {
	&:after {
		content: "";
		display: block;
		width: 2px;
		height: 100%;
		position: absolute;
		right: -1px;
		top: 0;
		z-index: 100;
		background: $white;
	}
}
.section {
	padding-top: 32px;
	font-size: $font-sm;
	min-height: calc(100vh - 335px);
	@include PC {
		min-height: calc(100vh - 423px);
	}
	@include PC-sm {
		min-height: calc(100vh - 428px);
	}
	@include Pad {
		font-size: $font-sm;
		min-height: calc(100vh - 460px);
	}
	@include mobile-lg {
		min-height: auto;
	}
	&-container {
		background: $white;
		border: 1px solid $gray200;
		padding: $w-md $w-xl;
		position: relative;
		@include Pad {
			padding: $w-md $w-lg;
		}
		&-id {
			position: absolute;
			width: 30px;
			height: 30px;
			display: flex;
			justify-content: center;
			align-items: center;
			background: $gray300;
			color: $white;
			border-radius: 0 $w-xxxs $w-xxxs 0;
			left: 0;
			top: $w-md;
			@include Pad {
				width: 25px;
				height: 25px;
			}
		}
	}
}
.section-backdrop {
    background-color: rgba(0, 0, 0, 0.1) !important; /* 半透明黑色背景 */
    z-index: 1020;
	pointer-events: none;
	opacity: 0;
	width: 100%;
	height: calc(100vh - 81px);
	position: fixed;
	top: 109px;
	&.active {
		opacity: 1;
	}
}
.section-combo {
	background: $white;
	border: 1px solid $gray300;
	border-top: 20px solid $primary-color;
	display: flex;
	@include PC-sm {
		flex-wrap: wrap;
		font-size: $font-xsm;
	}
	@include Pad {
		border-bottom: none;
	}
	&-item {
		width: 100%;
		padding: $w-md;
		position: relative;
		@include PC-sm {
			width: 60%;
		}
		@include Pad {
			width: 100%;
			border-right: none;
			border-bottom: 1px solid $gray300;
		}
		&:after {
			content: " ";
			background: $gray300;
			width: 1px;
			height: calc(100% - 32px);
			position: absolute;
			right: 0;
			top: $w-sm;
			@include Pad {
				display: none;
			}
		}
		&:nth-child(2) {
			width: 70%;
			@include PC-sm {
				width: 40%;
				&:after {
					display: none;
				}
			}
			@include Pad {
				width: 100%;
				border-bottom: none;
			}
		}
		&:last-child {
			width: 115px;
			flex-shrink: 0;
			@include PC-sm {
				width: 100%;
				display: flex;
				border-top: 1px solid $gray300;
			}
			&:after {
				display: none;
			}
			& > div {
				width: 100%;
				max-width: 90px;
			}
			.section-combo-title {
				font-size: $font-sm;
				@include PC-sm {
					margin-right: $w-sm;
				}
			}
		}
	}
	&-title {
		display: flex;
		align-items: center;
		font-size: $font-md;
		@include PC-sm {
			font-size: $font-smd;
		}
		@include mobile-md {
			flex-direction: column;
			align-items: start;
		}
		span {
			font-size: $font-sm;
			padding-left: $w-xs;
			margin-left: $w-xs;
			border-left: 1px solid $gray300;
			font-weight: $font-light;
			@include PC-sm {
				font-size: $font-xsm;
				padding-left: $w-xxs;
				margin-left: $w-xxs;
			}
			@include mobile-md {
				padding-left: 0;
				margin-left: 0;
				margin-top: $w-xxxs;
				border-left: none;
				color: $primary-color;
			}
		}
		img {
			@include mobile-md {
				margin-bottom: $w-xxxs;
			}
		}
	}
	&-info {
		background: $bright-color;
		padding: $w-sm;
		display: flex;
		align-items: center;
		&-title {
			color: $primary-color;
			font-size: $font-smd;
			flex-shrink: 0;
			padding-right: $w-sm;
			margin-bottom: 0;
			text-align: center;
			@include PC-sm {
				font-size: $font-sm;
				padding-right: $w-xs;
			}
		}
		&-item {
			font-weight: $font-bold;
			@include PC-sm {
				font-size: $font-xs;
			}
			@include mobile-lg {
				width: 100%;
			}
			li {
				margin-bottom: $w-xxxs;
			}
			span {
				padding-left: $w-xs;
				margin-left: $w-xs;
				border-left: 1px solid $gray300;
				font-weight: $font-light;
				@include PC-sm {
					padding-left: $w-xxs;
					margin-left: $w-xxs;
				}
				@include mobile-lg {
					display: block;
					width: 100%;
					border-left: 0;
					padding-left: 0;
					margin-left: 0;
					color: $primary-color;
				}
			}
		}
	}
	&-sign {
		display: flex;
		align-items: center;
		&-title {
			font-size: $font-sm;
			flex-shrink: 0;
			padding-right: $w-sm;
			margin-bottom: 0;
			@include PC-sm {
				font-size: $font-xsm;
			}
		}
		&-item {
			border: 1px solid $gray300;
			border-radius: $w-xs;
			box-shadow: 0 3px 3px $gray300;
			padding: $w-xs;
			width: 100%;
			max-width: 280px;
			&:hover {
				box-shadow: none;
			}
		}
		img.disable {
			filter: invert(91%) sepia(4%) saturate(0%) brightness(200%) contrast(87%);
		}
	}
}
.section-combo[data-con="manager"] {
	border-top: 20px solid $stable-color;
	.section-combo-title {
		img {
			filter: invert(49%) sepia(12%) saturate(2009%) hue-rotate(77deg) brightness(102%) contrast(60%);
		}
	}
	.section-combo-info {
		background: $stable-light-color;
		&-title {
			color: $stable-color;
		}
	}
}
.form-control,
.form-select {
	border: none;
	border-bottom: 1px solid $gray700;
	border-radius: 0;
	font-size: $font-sm;
	@include Pad {
		font-size: $font-xs;
	}
}
.form-control[data-con="upload"] {
	border-radius: $w-xxxs;
	background: $gray100;
	border: none;
}
textarea.form-control {
	border: 1px solid $primary-color;
	border-radius: $w-xxxs;
}
.form-check-input[type="checkbox"] {
	border-radius: 0.25em;
}
.col-form-label {
	font-weight: $font-heavy;
}
.form-check-label {
	font-size: $font-sm;
}
.form-select {
	background: url("../images/ic-dropdown.svg") no-repeat right center;
	font-size: $font-sm;
	@include Pad {
		font-size: $font-xs;
	}
}
.form-check-input:checked[type="checkbox"] {
	background: url("../images/ic-check.svg") no-repeat center $primary-color;
	background-size: 10px;
	border: 1px solid $primary-color;
}
.form-check-input[type="radio"] {
	border-radius: 50%;
	background-size: 10px;
	border: 1px solid $gray500;
}
.form-check-input:checked[type="radio"] {
	display: flex;
	justify-content: center;
	align-items: center;
	background: $white;
	&::before {
		content: "";
		position: absolute;
		background: $primary-color;
		width: 10px;
		height: 10px;
		border-radius: 50%;
	}
}
.input-group-text {
	background: none;
	height: 100%;
	border-radius: 0;
	border: 0;
	border-bottom: 1px solid $gray700;
}
.form-dropdown {
	position: relative;
	button {
		width: 100%;
		border: none;
		border-bottom: 1px solid $gray800 !important;
		border-radius: 0 !important;
		background: url("../images/ic-dropdown.svg") no-repeat right center $white !important;
		color: $gray800 !important;
		padding-right: $w-md;
		text-align: left;
		font-size: $font-sm;
		@include Pad {
			font-size: $font-xs;
		}
		&.active {
			color: $primary-color !important;
		}
	}
	&-content {
		position: absolute;
		left: 0;
		top: 100%;
		min-width: 350px;
		z-index: 10;
		@include PC-sm {
			left: auto;
			right: 0;
		}
	}
}
.no-file {
	background: $gray100;
	border: 1px dashed $gray500;
	color: $gray600;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 80px;
	@include Pad {
		font-size: $font-sm;
	}
}
.file-list {
	background: $gray100;
	border-radius: $w-xxxs;
	padding: $w-sm;
	&-item {
		display: flex;
		align-items: center;
		border-bottom: 3px solid $white;
		padding: 4px 0;
		@include Pad {
			flex-wrap: wrap;
			justify-content: center;
		}
		&:last-child {
			border: none;
		}
		&:hover {
			background: $white;
		}
	}
	&-info {
		border-right: 1px solid $gray500;
		font-size: $font-sm;
		width: 60px;
		text-align: center;
		padding: 0 $w-sm 0 $w-xs;
		@include Pad {
			width: 100%;
			border: 0;
		}
	}
	&-txt {
		padding-left: $w-sm;
		@include Pad {
			width: 100%;
			flex-shrink: 0;
			padding-left: 0;
			font-size: $font-sm;
		}
	}
}
