//banner
.topic[data-con="news"] {
    background: url("../images/new-banner.jpg") no-repeat left top;
    color: $gray800;
    padding-bottom: 0 !important;
    .topic-header {
        height: 160px;
        align-items: center;
        @include PC-sm {
            height: 150px;
        }
        @include Pad {
            height: 120px;
        }
        @include mobile-lg {
            height: 110px;
            flex-direction: column;
            justify-content: space-between;
            padding-top: $w-md;
            padding-bottom: $w-xxs;
        }
    }
    .breadcrumb {
        margin-top: 10%;
        @include PC-sm {
            margin-top: 100px;
        }
        @include Pad {
            margin-top: 80px;
        }
        @include mobile-lg {
            width: 100%;
            margin-top: 0;
        }
        &-link {
            color: $gray800 !important;
        }
    }
    .breadcrumb-item + .breadcrumb-item::before {
        background: url(../images/ic-route-dark.svg) no-repeat left center !important;
    }
    .breadcrumb-item.active {
        color: $gray800;
    }
}
/*--------------------側邊選單開始------------------*/
.side-menu {
    width: 20%;
    margin-right: 48px;
    flex-shrink: 0;
    transition: all 0.2s ease;
    z-index: 20;
    position: relative;
    border-right: 1px solid $gray300;
    @include PC-lg {
        width: 220px;
        margin-right: 32px;
    }
    @include PC-sm {
        width: 100%;
        display: flex;
        border-right: 0;
    }
    &-nav {
        display: flex;
        flex-direction: column;
        @include PC-sm {
            width: 100%;
            flex-direction: row;
        }
        @include Pad {
            border-bottom: 1px solid $primary-color;
        }
    }
    &-item {
        @include PC-sm {
            flex-shrink: 0;
        }
    }
    &-link {
        display: flex;
        align-items: center;
        font-size: $font-smd;
        color: $gray800;
        padding: $w-xxs $w-md;
        margin-bottom: $w-md;
        @include PC-sm {
            margin-bottom: 0;
            flex-direction: column;
            font-size: $font-sm;
        }
        @include Pad {
            padding: $w-xxxs $w-sm;
        }
        &:hover {
            color: $gray600;
        }
        &.active {
            color: $white;
            background: $primary-color;
            border-radius: $w-xxxs 0 0 $w-xxxs;
            @include PC-sm {
                border-radius: $w-xxxs $w-xxxs 0 0;
            }
            img {
                filter: invert(80%) sepia(43%) saturate(17%) hue-rotate(23deg) brightness(500%) contrast(95%);
            }
        }
        img {
            width: 24px;
            margin-right: $w-xs;
            filter: invert(80%) sepia(43%) saturate(17%) hue-rotate(23deg) brightness(100%) contrast(95%);
            @include PC-sm {
                margin-bottom: $w-xxxs;
                margin-right: 0;
            }
        }
    }
}
.side-menu[data-con="map"] {
    position: fixed;
    left: 0;
    width: 380px;
    height: calc(100vh - 79px);
    top: 110px;
    background: $white;
    transition: all 0.2s ease;
    z-index: 1000;
    border-right: none;
    box-shadow: 2px 2px 2px $gray500;
    @include PC {
        height: calc(100vh - 110px);
    }
    @include PC-sm {
        width: 310px;
        height: calc(100vh - 99px);
        top: 99px;
    }
    @include Pad {
        height: calc(100vh - 91px);
        top: 91px;
    }
    .side-menu-toggler {
        display: flex;
        padding: 0;
        width: 32px;
        height: 56px;
        background: $secondary-color;
        border-radius: 0 8px 8px 0 !important;
        position: absolute;
        right: -32px;
        top: $w-xl;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        z-index: 10;
        @include Pad {
            width: 24px;
            right: -24px;
            height: 56px;
            top: 130px;
        }
        &:hover {
            background: $primary-xlight-color;
        }
        img {
            width: 100%;
            transform: rotate(0deg);
        }
    }
    iframe {
        height: 100%;
    }
    &.close {
        left: -380px;
        @include PC-sm {
            left: -310px;
        }
        .side-menu-toggler {
            img {
                transform: rotate(180deg);
            }
        }
    }
}
.side-menu[data-position="right"] {
    left: auto;
    right: 0;
    margin-right: 0;
    .side-menu-toggler {
        right: auto;
        left: -32px;
        border-radius: 8px 0 0 8px !important;
        @include Pad {
            right: auto;
            left: -24px;
        }
        img {
            transform: rotate(180deg);
        }
    }
    &.close {
        left: auto;
        right: -380px;
        @include PC-sm {
            left: auto;
            right: -310px;
        }
        img {
            transform: rotate(0deg) !important;
        }
    }
}
.side-menu-header {
    display: flex;
    justify-content: center;
    background: $primary-xlight-color;
    border-bottom: 2px solid $primary-color;
    color: $primary-color;
    padding: $w-xs 0;
    font-size: $font-md;
    position: sticky;
    top: 0;
    z-index: 100;
    @include Pad {
        padding: $w-xxs 0;
        font-size: $font-smd;
    }
    a {
        position: absolute;
        left: $w-sm;
        top: 12px;
        @include Pad {
            top: 8px;
        }
    }
}
.side-menu-info {
    background: $gray500;
    color: $white;
    text-align: center;
    padding: 2px $w-sm;
    font-weight: $font-light;
    position: sticky;
    top: 57px;
    @include Pad {
        font-size: $font-xsm;
        top: 45px;
    }
}
.side-menu-body {
    @include mobile-sm{
        .accordion-body{
            font-size: $font-xs !important;
        }
    }
    &-title {
        display: flex;
        align-items: start;
        font-size: $font-smd;
        margin-bottom: 0;
        font-weight: $font-heavy;
        color: $black;
        @include mobile-sm{
            font-size: $font-sm;
        }
        img {
            margin-right: $w-xxs;
            filter: brightness(50%);
        }
    }
    .form-check-label {
        display: flex;
        align-items: center;
        @include mobile-sm{
            img{
            width: 22px;
            margin-right: 4px !important;
        }
        }
    }
    .nav-tabs {
        background: $gray300;
        padding: $w-xs $w-xs 0 $w-xs;
        border-bottom: none;
        .nav-link {
            font-size: $font-sm;
            background: $gray100;
            margin-right: $w-xs;
            padding: $w-xs $w-xxxs !important;
            @include mobile-sm {
                font-size: $font-xsm;
                margin-right: $w-xxxs;
            }
            &:hover {
                background: $white !important;
            }
            &:last-child {
                margin-right: 0;
            }
            &.active {
                background: $white;
                pointer-events: none;
                &::after {
                    bottom: 5px;
                    width: calc(100% - 24px);
                    left: 12px;
                }
            }
        }
    }
}
.side-menu-form {
    display: flex;
    align-items: center;
    label {
        flex-shrink: 0;
        margin-right: $w-xs;
        margin-bottom: 0;
        width: 66px;
        text-align: right;
        font-size: $font-sm;
        font-weight: $font-bold;
    }
    .form-control,
    .form-select {
        border: 1px solid $primary-color !important;
        border-radius: $w-xxxs;
        font-size: $font-xsm;
    }
    .form-select {
        background: url("../images/ic-dropdown-width.svg") no-repeat right center;
    }
    .form-number::-webkit-inner-spin-button,
    .form-number::-webkit-outer-spin-button {
        display: none !important;
    }

    .form-number:hover::-webkit-inner-spin-button,
    .form-number:hover::-webkit-outer-spin-button {
        display: none;
    }
    .form-number {
        position: relative;
        input {
            padding-right: 2px;
        }
        .arrow-up,
        .arrow-down {
            position: absolute;
            display: block;
            width: 20px;
            height: 13px;
            right: 2px;
            cursor: pointer;
            border: none !important;
            &:active,
            &:focus {
                border: none !important;
            }
        }
        .arrow-up {
            top: 6px;
            background: url("../images/ic-up-arrow.svg") no-repeat center $white;
            background-size: 8px;
        }

        .arrow-down {
            bottom: 6px;
            background: url("../images/ic-down-arrow.svg") no-repeat center $white;
            background-size: 8px;
        }
    }
}
.side-menu-list {
    &-item {
        border-bottom: 1px solid $gray300;
        &:last-child {
            border-bottom: none;
        }
    }
    &-link {
        display: flex;
        color: $gray900;
        padding: $w-sm $w-md;
        @include mobile-sm{
            padding: $w-sm $w-sm;
        }
        &:hover {
            background: $primary-light-color;
            color: $gray900;
        }
    }
    &-content {
        width: 100%;
        @include mobile-sm{
            color: $gray800;
        }
    }
    &-title {
        display: flex;
        font-size: $font-smd;
        align-items: end;
         color: $gray900;
        @include mobile-sm {
            flex-wrap: wrap;
            font-size: $font-xsm;
        }
        span {
            border-left: 1px solid $gray300;
            padding-left: $w-xs;
            margin-left: $w-xs;
            font-size: $font-xsm;
            line-height: 1;
            margin-top: $w-xxxs;
            @include mobile-sm{
                padding-left: $w-xxs;
            margin-left: $w-xxs;
                font-size: $font-xxs;
            }
        }
    }
    &-icon {
        flex-shrink: 0;
    }
    p {
        @include PC {
            font-size: $font-xs;
        }
        & ~ span {
            font-size: $font-xs;
            padding-left: 28px;
        }
    }
}
.side-menu-list[data-con="company"] {
    .side-menu-list-item {
        &:last-child {
            border-bottom: 1px solid $gray300;
        }
    }
}
.side-menu-footer {
    background: $white;
    position: sticky;
    bottom: 30px;
    display: flex;
    @include mobile-sm{
        bottom: 0;
    }
    &[data-num="odd"] {
        & > * {
            width: 100% !important;
            margin-right: 16px !important;
        }
        & > *:last-child {
            margin-right: 0 !important;
        }
        @include mobile-sm {
            flex-wrap: wrap;
            & > * {
                width: calc(50% - 8px) !important;
                margin-right: 16px;
            }
            & > *:nth-child(even) {
                margin-right: 0 !important;
            }
            & > *:last-child {
                width: 100% !important;
                margin-right: 0 !important;
            }
        }
    }
}
.side-menu-btn {
    font-size: $font-sm !important;
}
.ml-open {
    //地圖頁麵包屑
    margin-left: 425px;
    @include PC-lg {
        margin-left: 320px;
    }
    @include PC {
        margin-left: 0;
    }
}
.mr-open {
    //地圖頁麵包屑
    margin-right: 425px;
    @include PC-lg {
        margin-right: 320px;
    }
    @include PC {
        margin-right: 0;
    }
}
/*--------------------內容開始------------------*/
.side-content {
    background: $white;
    border-radius: 12px;
    width: 100%;
    &[data-con="map"] {
        height: calc(100vh - 110px);
    }
}
/*--------------------news開始------------------*/
.table-rwd[data-con="news"] {
    @include Pad {
        td:nth-child(1) {
            width: 100%;
        }
        td:nth-child(2) {
            width: 100%;
            color: $gray800 !important;
            p {
                text-align: left;
                margin-bottom: 12px;
            }
        }
    }
}
/*--------------------內頁開始------------------*/
.carousel-indicators [data-bs-target] {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: 1px solid $primary-color;
    background: $white;
    margin: 0 $w-xxs $w-sm $w-xxs;
    opacity: 1;
    &.active {
        background: $primary-color;
    }
}
.carousel-control-prev,
.carousel-control-next {
    width: 60px;
    height: 60px;
    background: $primary-color;
    border-radius: 50%;
    opacity: 1;
    top: calc(50% - 30px);
    @include PC-md {
        width: 50px;
        height: 50px;
        top: calc(50% - 25px);
    }
    @include PC-sm {
        width: 40px;
        height: 40px;
        top: calc(50% - 20px);
    }
    img {
        width: 50%;
    }
}
.carousel-control-prev {
    left: 40px;
    @include PC {
        left: 20px;
    }
    img {
        transform: scaleX(-1);
    }
}
.carousel-control-next {
    right: 40px;
    @include PC {
        right: 20px;
    }
}
.page-slide {
    position: relative;
    z-index: 1;
    &-item {
        overflow: hidden;
        justify-content: center;
        align-items: flex-end;
        @include PC-sm {
            height: 500px;
        }
        @include Pad {
            height: 690px;
            align-items: end;
        }
        @include mobile-lg {
            height: 650px;
        }
        @include mobile-md {
            height: 600px;
        }
        &.active {
            display: flex;
        }
        & > img {
            @include PC-lg {
                margin-left: 18%;
            }
            @include PC {
                width: 1200px;
                max-width: 1200px;
                margin-left: 30%;
            }
            @include PC-sm {
                width: 1000px;
            }
            @include Pad {
                width: 768px;
                margin-left: 0;
            }
            @include mobile-lg {
                width: 650px;
            }
            @include mobile-md {
                width: 600px;
            }
        }
    }
    &-item[data-con="plan"] {
        @include PC-sm {
            align-items: start;
            height: auto;
        }
        img {
            @include PC-lg {
                width: 100%;
                max-width: 100%;
                margin-left: 0;
            }
            @include PC-sm {
                margin-top: 20px;
            }
            @include Pad {
                margin: 0 auto;
                margin-bottom: 50px;
            }
            @include mobile-lg {
                max-width: 390px;
                margin-bottom: 20px;
            }
        }
        .page-slide-caption {
            position: static;
            display: flex;
            width: 100%;
            justify-content: flex-start;
            margin-top: -10px;
            margin-bottom: 5%;
            @include Pad {
                margin-top: 50px;
                margin-bottom: 0;
            }
        }
        .page-slide-caption-title {
            font-size: $w-xl;
            transform: scale(1);
            @include PC {
                font-size: $w-lg;
            }
            @include Pad {
                margin-left: 0;
                text-align: left;
            }
        }
    }
    &-en {
        font-size: 4.5rem;
        font-weight: $font-bold;
    }
    &-highlight {
        background: $danger-dark-color;
        color: $white;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        vertical-align: middle;
        width: 100px;
        height: 100px;
        margin-left: $w-xs;
        margin-top: -18px;
        border-radius: 50%;
        text-shadow: none;
    }
    &-caption {
        color: $gray900;
        top: 27%;
        bottom: auto;
        text-align: left;
        @include PC-lg {
            top: 25%;
            left: 12%;
            right: 12%;
        }
        @include PC-md {
            top: 18%;
            left: 5%;
            right: 5%;
        }
        @include PC {
            left: -50px;
            right: 0;
        }
        @include PC-sm {
            top: 18%;
            left: -100px;
        }
        @include Pad {
            left: 0;
            right: 0;
            top: 15px;
            text-align: center;
        }
        &[data-style="normal"] {
            .page-slide-caption-title {
                line-height: 1.2;
                margin-bottom: $w-sm;
            }
            .page-slide-caption-txt {
                line-height: 1.5;
            }
        }

        &-title {
            font-size: 4rem;
            font-weight: $font-heavy;
            line-height: 1.35;
            margin-bottom: 0;
            text-shadow: 0 0 10px $white;
            @include PC-md {
                transform: scale(0.8);
            }
            @include PC {
                transform: scale(0.7);
            }
            @include PC-sm {
                transform: scale(0.6);
            }
            @include Pad {
                width: 130%;
                margin-left: -15%;
            }
            @include mobile-lg {
                width: 160%;
                margin-left: -30%;
            }
            @include mobile-md {
                transform: scale(0.5);
            }
            @include mobile-sm {
                transform: scale(0.45);
                width: 200%;
                margin-left: -50%;
            }
        }
        &-txt {
            font-size: $font-md;
            font-weight: $font-heavy;
            letter-spacing: 1px;
            line-height: 1.1;
            @include PC-md {
                transform: scale(0.8);
                margin-top: -20px;
            }
            @include PC {
                transform: scale(0.75);
                margin-top: -30px;
                margin-left: 38px;
            }
            @include PC-sm {
                margin-top: -40px;
                margin-left: 9%;
            }
            @include Pad {
                width: 150%;
                margin-left: -25%;
            }
            @include mobile-lg {
                transform: scale(0.7);
                margin-top: -40px;
            }
            @include mobile-md {
                width: 140%;
                margin-left: -20%;
                margin-top: -50px;
            }
            @include mobile-sm {
                width: 140%;
                margin-top: -55px;
                margin-left: -20%;
                br {
                    display: none;
                }
            }
            &-lg {
                font-size: $font-xl;
                font-weight: $font-xheavy;
            }
        }
    }
    &-action {
        display: flex;
        @include PC-md {
            margin-left: 10%;
            margin-top: -10px;
        }
        @include PC {
            margin-left: 15%;
        }
        @include PC-sm {
            margin-left: 20%;
            margin-top: -15px;
        }
        @include Pad {
            justify-content: center;
            margin-left: 0;
        }
        @include mobile-md {
            margin-top: -15px;
        }
        .btn {
            width: 220px;
            height: 60px;
            line-height: 60px;
            padding: $w-sm 0;
            font-size: $font-md;
            font-weight: $font-bold;
            @include PC-md {
                width: 170px;
                height: 55px;
                line-height: 55px;
                font-size: $font-smd;
            }
            @include PC-sm {
                height: 50px;
                line-height: 50px;
                font-size: $font-sm;
                width: 140px;
            }
            @include mobile-lg {
                width: 125px;
            }
        }
    }
}
.building {
    &-on {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: $w-sm;
        background: $gray100;
        .building-floor{
            margin-top: -1px;
        }
    }
    &-header{
        width: 90%;
    }
    &-none{
        display: flex;
        justify-content: center;
        align-items: center;
        color: $gray600;
        width: 100%;
        @include mobile-sm{
            font-size: $font-xxs;
            img{
                width: 20px;
            }
        }
    }
    &-floor {
        width: 85%;
        border: 1px solid $gray600;
        &:after {
            content: "";
            height: 7px;
            display: block;
            width: calc(100% + 10px);
            background: $gray300;
            border: 1px solid $gray600;
            margin: -1px 0 -2px -5px;
        }
        &-item {
            background: $white;
            border-bottom: 1px solid $gray600;
            &:last-child {
                border-bottom: none;
            }
            &[data-con="danger"]{
                background: $primary-xlight-color;
                .building-floor-title{
                    span{
                        color: $primary-dark-color;
                    }
                }
            }
        }
        &-link {
            display: flex;
            align-items: center;
            color: $gray900;
            padding: $w-xxs $w-sm;
            position: relative;
            overflow: hidden;
            @include mobile-sm{
                padding: $w-xxs $w-xs;
            }
            &:after{
                    content: "";
                    display: block;
                    position: absolute;
                    width: 4px;
                    height: 100%;
                    background: $primary-color;
                    left: -8px;
                    top: 0;
                }
            &:hover {
                color: $gray900;
                &:after{
                    left: 0;
                }
            }
        }
        &-title {
            display: flex;
            flex-direction: column;
            align-items: center;
            flex-shrink: 0;
            margin-right: $w-xxs;
            font-size: $font-xs;
            @include mobile-sm{
                font-size: $font-xxs;
            }
            span {
                font-size: $font-md;
                font-weight: $font-heavy;
                line-height: 1;
            }
        }
        &-map {
            width: 100%;
            height: 45px;
            border: 1px solid $gray300;
            overflow: hidden;
            img {
                width: 100%;
            }
        }
    }
    &-sign {
        display: flex;
        width: 100%;
        justify-content: center;
        li {
            width: 28px;
            height: 28px;
            margin-right: $w-sm;
            @include mobile-sm{
                width: 25px;height: 25px;
                margin-right: $w-xs;
            }
            &:last-child {
                margin-right: 0;
            }
            img {
                width: 100%;
            }
        }
        [data-con="fire"] {
            background: url("../images/ic-fire-none.svg");
            background-size: cover;
        }
        [data-con="poison"] {
            background: url("../images/ic-poison-none.svg");
            background-size: cover;
        }
        [data-con="nowater"] {
            background: url("../images/ic-nowater-none.svg");
            background-size: cover;
        }
    }
    &-under {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: $w-sm;
        background: $gray100;
        .building-floor {
            &:after {
                display: none;
            }
            &-link{
                background: $gray100;
            }
        }
    }
    &-factory {
        min-width: 42px;
        height: 42px;
        flex-shrink: 0;
        background: url("../images/ic-factory.svg") no-repeat;
        background-size: 33px;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        margin-right: $w-xxs;
        span {
            background: $white;
            border: 1px solid $gray700;
            border-radius: 50px;
            padding: 0 $w-xxxs;
            min-width: 23px;
            height: 23px;
            text-align: center;
            line-height: 1.3;
        }
    }
    &-danger-list{
        display: flex;
        justify-content: center;
        img{
            @include mobile-sm{
            width: 20px;
        }
        }
    }
}
.building[data-con="import"]{
    width: 100%;
    .building-on,.building-under{
        padding-right: 30px;
    }
    .building-header{
        width: 90%;
    }
    .building-floor{
        width: 85%;
    }
    .building-floor-item{
        display: flex;
         padding: $w-xxs $w-sm;
         background: $white !important;
         position: relative;
            @include mobile-md{
                padding: $w-xxs $w-xs;
            }
            @include mobile-md{
                flex-direction: column;
                font-size: $font-xsm;
            }
    }
    .building-floor-title{
        margin-right: $w-xs;
        width: 80px;
        @include mobile-md{
            width: 100%;
            margin-right: 0;
            margin-bottom: $w-xxs;
            flex-direction: row;
            }
    }
    .building-floor-form{
            border: 1px solid $primary-color;
            border-radius: $w-xxxs;
            width: 56px;
            height: 26px;
            padding: 0 $w-xxxs;
            text-align: center;
            font-weight: $font-heavy;
            font-size: $font-smd;
             @include mobile-md{
                margin-right: $w-xs;
             }
             &::placeholder{
            font-weight: $font-light;
            color: $gray600 !important;
        }
        }
    .building-floor-edit{
        border-radius: 0;
        padding: $w-xxxs 0 2px 0;
        border: 0;
        border-bottom: 1px solid $primary-color;
        font-size: $font-xxs;
        background: url("../images/ic-edit.svg") no-repeat right center;
        background-size: 18px;
        &::placeholder{
            font-size: $font-xxs;
            font-weight: $font-light;
            color: $gray600 !important;
        }
        img{
            margin-left: $w-xxxs;
            width: 18px;
        }
    }
    .building-import{
        position: absolute;
        right: -40px;
        top: calc(50% - 13px);
        width: 26px;
        height: 26px;
        padding: 0;
        border-radius: $w-xxxs;
        img{
            margin-right: 0;
        }
    }
    .building-btn{
        border: 1px solid $primary-color;
        padding: 0 $w-xs;
        line-height: 1.2;
        font-weight: $font-heavy;
        font-size: $font-smd;
        border-radius: $w-xxxs;
        &:hover{
            background: $primary-color;
            color: $white;
        }
    }
    .building-none{
        justify-content: flex-start;
          @include mobile-md{
        font-size: $font-xs;
        align-items: flex-start;
    }
    }
    .building-floor:after{
        margin-top: 0;
    }
}
@each $key, $value in $color-themes {
    .building-txt-#{$key} {
    display: flex;
    flex-wrap: wrap;
    li {
        border-left: 4px solid $value;
        padding-left: $w-xxxs;
        line-height: 1;
        font-size: $font-xxs;
        margin-bottom: $w-xxxs;
    }
}
}