/*--溢出文字點點點--*/
@mixin over_text($line) {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
}

/*--隱藏區塊內文字--*/
@mixin text-indent {
    text-indent: -9999px;
    overflow: hidden;
    white-space: nowrap;
}
/* --------------按鈕設定開始--------------- */
@mixin btn-dark($bgcolor) {
    color: $white !important;
    background: $bgcolor !important;
    border: 1px solid $bgcolor;
    padding: 4px 16px;
    border-radius: $w-xxs;
    font-size: $font-xs;
    transition: all 0.2s ease;
    &:hover {
        color: $white !important;
        filter: brightness(80%) contrast(180%);
    }
    img {
        margin-right: 4px;
    }
}
@mixin btn-border($color) {
    color: $color !important;
    background: $white !important;
    border: 2px solid $gray300 !important;
    border-radius: $w-xxs;
    position: relative;
    padding: 3px $w-sm;
    font-size: $font-xs;
    overflow: hidden;
    position: relative;
    transform: scale(1);
    transition: all 0.2s ease;
    transition-property: all !important;
    display: inline-flex !important;
    &:before{
        background: $gray100 !important;
    }
    img {
        margin-right: $w-xxxs;
    }
    &:hover {
        border: 2px solid $color !important;
        color: $color !important;
        transform: scale(1.01);
    }
}
@mixin btn-default($color){
    border-radius: $w-xxs !important;
    font-size: $font-xs;
    background: $color !important;
    color: $white !important;
    overflow: hidden;
    vertical-align: middle;
    transform: scale(1);
    transition: all 0.2s ease;
    transition-property: all !important;
    &:before {
        filter: brightness(80%) contrast(180%);
    }
    &:hover{
        transform: scale(1.01);
    }
    img{
        margin-right: 4px;
    }
}
@mixin btn-card($bgcolor) {
    border-radius: $w-xxs;
    overflow: hidden;
    display: flex;
    border: 1px solid $bgcolor;
    color: $gray800;
    min-height: 40px;
    &:hover{
        color: $gray800;
        .btn-card-body{
            background: lighten($color: $bgcolor, $amount: 43%);
        }
    }
    .btn-card-title{
        background: $bgcolor;
        color: $white;
        padding: $w-xxs $w-xs;
        flex-shrink: 0;
    }
    .btn-card-body{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: $w-xxs $w-xs;
        width: 100%;
        background: $white;
        img{
            width: $w-sm;
            margin-left: $w-xs;
        }
        span{
            border-left: 1px solid $gray300;
            padding-left: $w-xxs;
            margin-left: $w-xxs;
        }
    }
}
