/*--------------------首頁開始------------------*/
//banner
.topic {
    background: url("../images/index-banner.jpg") no-repeat right top $gray100;
    background-size: contain;
    color: $white;
    @include PC-lg {
        background-size: 1600px;
    }
    @include PC {
        background-size: 1500px;
    }
    @include Pad {
        background-size: 1200px;
    }
    @include mobile-lg {
        background: url("../images/index-banner.jpg") no-repeat -500px top $gray100;
        background-size: 1100px;
    }
    &-header {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        padding: $w-md $w-lg;
        margin-bottom: $w-xs;
        @include PC-lg {
            padding: $w-sm $w-lg;
            margin-bottom: $w-md;
        }
        @include Pad {
            margin-bottom: 0;
        }
        @include mobile-lg {
            flex-direction: column-reverse;
            align-items: center;
            padding-left: 0;
            padding-right: 0;
        }
    }
    &-logo {
        width: 80%;
        max-width: 230px;
        @include PC {
            max-width: 200px;
        }
        @include Pad {
            max-width: 160px;
        }
        @include mobile-lg {
            max-width: 150px;
        }
    }
    &-slider {
        position: relative;
        padding: 0 48px;
        @include Pad {
            padding: 0 40px;
        }
        @include mobile-lg {
            padding: 0 20px;
        }
        &-item {
            border-radius: $w-sm;
            height: 230px;
            background: rgba(255, 255, 255, 0.9);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            color: $gray900;
            font-size: $font-md;
            @include PC {
                font-size: $font-smd;
                height: 200px;
            }
            @include Pad {
                font-size: $font-sm;
            }
            @include mobile-lg {
                justify-content: start;
                height: 160px;
                padding: $w-lg $w-md;
                text-align: center;
            }
            @include mobile-md {
                text-align: center;
            }
            &:hover {
                background: rgba(255, 255, 255, 1);
                color: $primary-color;
            }
            img {
                width: 64px;
                @include PC {
                    width: 56px;
                }
                @include Pad {
                    width: 48px;
                }
                @include mobile-lg {
                    width: 40px;
                }
            }
        }
    }
}
section[data-con="links"] {
    background: url("../images/index-footer-bg.jpg") no-repeat center top;
    background-size: 100%;
    padding-top: 180px;
    padding-bottom: 80px;
    margin-bottom: -72px;
    @include PC {
        padding-top: 130px;
    }
    @include PC-sm {
        background-size: 1000px;
        padding-top: 100px;
    }
}
.link-slider {
    padding: 0 48px;
    &-item {
        display: flex;
        justify-content: center;
        @include Pad {
            padding: 0 $w-sm;
        }
    }
    .slick-dots {
        bottom: -50px;
    }
}
//視窗頁
.window {
    &-wrapper {
        min-height: calc(100vh - 232px);
        padding: 80px 0;
        margin-bottom: -72px;
        display: flex;
        justify-content: center;
        align-items: center;
        @include PC {
            min-height: calc(100vh - 320px);
            padding: 50px 0;
        }
        @include Pad {
            padding: 40px 0 60px 0;
            min-height: auto;
        }
    }
    &-box {
        padding: 54px 20%;
        background: $white;
        border: 1px solid $gray300;
        @include PC-sm {
            padding: 48px 13%;
        }
        @include Pad {
            padding: 32px 10%;
        }
    }
    &-box-sm{
        padding: 54px 28%;
        @include PC-sm {
            padding: 48px 20%;
        }
        @include Pad {
            padding: 32px 15%;
        }
        @include mobile-lg {
            padding: 32px 10%;
        }
    }
}
.login {
    &-notice {
        font-size: $font-xsm;
    }
    &-btn {
        height: 48px;
        font-size: $font-smd !important;
    }
    &-title {
        font-size: $font-smd;
        font-weight: $font-light;
        text-align: center;
    }
    &-code {
        display: flex;
        height: 48px;
        & > * {
            height: 100%;
        }
        img {
            width: 100%;
            flex-shrink: 1;
        }
        .btn {
            width: 48px;
            flex-shrink: 0;
            padding: $w-xxxs;
            img {
                margin-right: 0;
            }
        }
    }
    .btn {
        font-size: $font-sm;
        height: 48px;
    }
    .form-control {
        border: 1px solid $primary-color;
        border-radius: $w-xxs;
        height: 48px;
        &::placeholder {
            color: $gray700 !important;
            font-size: $font-sm;
        }
        &[data-con="account"] {
            background: url("../images/ic-account-circle.svg") no-repeat $w-xs center;
            padding-left: $w-xl;
        }
        &[data-con="password"] {
            background: url("../images/ic-password.svg") no-repeat $w-xs center;
            padding-left: $w-xl;
        }
        &[data-con="email"] {
            background: url("../images/ic-email.svg") no-repeat $w-xs center;
            padding-left: $w-xl;
        }
        &[data-con="verification"] {
            background: url("../images/ic-verification.svg") no-repeat $w-xs center;
            padding-left: $w-xl;
        }
    }
    .input-group {
        border-bottom: 0;
        input {
            border-right: 0;
        }
        .btn[data-con="view"] {
            border-radius: $w-xxs;
            width: 48px;
            height: 48px;
            border: 1px solid $primary-color !important;
            border-left: 0 !important;
            text-indent: -9999px;
            background: url("../images/ic-visibility.svg") no-repeat;
            background-size: 100%;
            transition: all 0.2s ease;
            &.active {
                background-position: bottom;
            }
        }
    }
    .btn[data-con="send"] {
        &.active {
            background: $gray200 !important;
            border: 1px solid $gray400;
            color: $gray800 !important;
            pointer-events: none;
        }
    }
}
//otp
.otp {
    text-align: center;
    &-txt {
        font-size: $font-md;
        @include PC-sm {
            font-size: $font-smd;
        }
    }
    &-notice {
        color: $primary-color;
        display: inline-block;
        @include PC-sm {
            font-size: $font-xs;
        }
    }
    &-number {
        border: 1px solid $gray400;
        border-radius: $w-xxxs;
        font-size: 50px;
        margin: 0 auto;
        letter-spacing: 2px;
        padding: 2px 0 4px 0;
    }
    &-btn {
        display: flex !important;
        justify-content: center;
        height: 48px;
        font-size: $font-smd !important;
    }
    &-form {
        display: flex;
        align-items: stretch;
        @include Pad {
            flex-direction: column;
        }
        & ~ p {
            padding-left: 80px;
            font-size: $font-xsm;
            @include Pad {
                padding-left: 0;
            }
        }
    }
    &-label {
        width: 80px;
        display: flex;
        align-items: center;
        font-weight: $font-heavy;
        flex-shrink: 0;
        margin-bottom: 0;
    }
    .btn {
        flex-shrink: 0;
    }
    .form-control {
        border: 1px solid $primary-color;
        border-radius: $w-xxxs;
    }
}
//message
.message{
    min-height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    padding-bottom: 82px;
    @include Pad {
        min-height: 400px;
    }
    &-sign{
        @include Pad {
            width: 70px;
        }
    }
    &-title{
        display: flex;
        flex-direction: column;
        align-items: center;
        color: $primary-color;
    }
    &-text{
        text-align: center;
        @include Pad{
            font-size: $font-xsm;
        }
        @include mobile-lg{
            text-align: left;
            br{
                display: none;
            }
        }
    }
    &-notice{
        background: $primary-color;
        color: $white;
        padding: 4px 20%;
        text-align: center;
        position: absolute;
        left: 0;
        bottom: 90px;
        width: 100%;
        @include PC-sm {
            padding: 4px 13%;
        }
        @include Pad {
            padding: 6px 10%;
            bottom: 60px;
            font-size: $font-xsm;
        }
    }
}